<template>
  <div class="wrap">
    <img src="../assets/images/mall/write.png" alt="">
    <div class="wrap_msg">正在建设中，稍后访问</div>
  </div>
</template>

<script>
    export default {
        name: "writing"
    }
</script>

<style scoped>
  .wrap {
    position: relative;
  }
  .wrap img {
    position: absolute;
    top: 167.5px;
    left: 0;
    right: 0;
    margin: auto;
    width: 91px;
    height: 105px;
    -webkit-transform: translate(10%, 0);
    -moz-transform: translate(10%, 0);
    -ms-transform: translate(10%, 0);
    -o-transform: translate(10%, 0);
    transform: translate(10%, 0);
  }
  .wrap .wrap_msg {
    position: absolute;
    top: 279px;
    width: 100%;
    font-size: 15.5px;
    font-family: PingFangSC-Medium;
    font-weight: 500;
    color: rgba(167, 167, 167, 1);
    line-height: 22px;
    letter-spacing: 1px;
    text-align: center;
  }
</style>



